<template>
  <div class="live-index">
    <div class="tips flex-between">
      <div class="tip-item">
          <div class="icon-wrap" :style="{background: '#5F44E8'}"><i class="icon-block iconfont"></i></div>
          <div class="info-wrap">
            <div>{{liveTotal}}</div>
            <div>累计直播</div>
          </div>
      </div>
      <div class="tip-item">
          <div class="icon-wrap" :style="{background: '#43B6F5'}"><i class="icon-video2 iconfont"></i></div>
          <div class="info-wrap">
            <div>{{livingNumber}}</div>
            <div>正在直播</div>
          </div>
      </div>
      <div class="tip-item">
          <div class="icon-wrap" :style="{background: '#F77809'}"><i class="icon-schedule iconfont"></i></div>
          <div class="info-wrap">
            <div>{{aptNumber}}</div>
            <div>直播预告</div>
          </div>
      </div>
      <div class="tip-item">
          <div class="icon-wrap" :style="{background: '#30B394'}"><i class="icon-user iconfont"></i></div>
          <div class="info-wrap">
            <div>{{lookerNumber}}</div>
            <div>观看人数</div>
          </div>
      </div>
    </div>
    <!-- 正在直播 -->
    <div  class="live-ing">
      <div class="title">正在直播</div>
      <div v-if="livingListFour.length>0" class="live-wrap">
        <liveBox @click.native="handleToLiving(item)" :info="item" v-for="item in livingListFour" :key="item.id"></liveBox>
      </div>
    </div>
    <!-- 直播预告 -->
    <div  class="live-ing">
      <div class="title">直播预告</div>
      <div v-if="appLiveListFour.length>0" class="live-wrap">
        <liveBroad :info="item" v-for="item in appLiveListFour" :key="item.id"></liveBroad>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  components:{
    liveBox:()=>import("@/components/live-box"),
    liveBroad:()=>import("@/components/live-broad")
  },
  data(){
    return {
      appLiveList: [],
      livingList: [],
      liveTotal: 0,
      livingNumber: 0,
      aptNumber: 0,
      lookerNumber: 0
    }
  },
  created(){
    this.init()
  },
  methods:{
    init(){
      this.$api.getIndexData().then(res => {
        const {appLiveList,livingList,liveTotal,livingNumber,aptNumber,lookerNumber} = res.data
        this.appLiveList = appLiveList;
        this.livingList = livingList;
        this.liveTotal = liveTotal;
        this.livingNumber = livingNumber;
        this.aptNumber = aptNumber;
        this.lookerNumber = lookerNumber;
      })
    },
    /* 跳转直播中心 */
    handleToLiving(item){
      this.$router.push(`/liveCenter/video/play/${item.id}/${item.code}`)
    }
  },
  computed:{
    livingListFour(){
      return this.livingList.length > 4 ? this.livingList.splice(0,4) : this.livingList;
    },
    appLiveListFour(){
      return this.appLiveList.length > 4 ? this.appLiveList.splice(0,4) : this.appLiveList;
    }
  }
}
</script>

<style lang="scss" scoped>
.live-index {padding: 32px;min-height: 100%;}
.tips .tip-item {
  display: flex;align-items: center;
  border-radius: 6px;padding-left: 40px;
  height: 140px;background: #fff;
  flex-grow: 1;
  margin-right: 30px;
}
.tips .tip-item:last-child{margin-right: 0}
.icon-wrap {
  width: 68px;height: 68px;text-align: center;
  border-radius: 50%;line-height: 68px;
}
.icon-wrap i {font-size: 40px;color: #fff;}
.info-wrap > div {padding-left: 19px;}
.info-wrap div:nth-child(1) {color: #292C33;font-size: 30px}
.info-wrap div:nth-child(2) {color: #909399;}

.live-ing {margin-top: 32px;}
.live-ing >.title {color: #606266;line-height: 1;padding-bottom: 20px}
.live-ing .live-wrap {margin-left: -15px;margin-right: -15px;display: flex;flex-wrap: wrap;}
.live-ing .live-wrap > div {width: 25%;margin-bottom: 20px;}
</style>